  background-size: cover;
}

/* ASK SUPERHOST FLEX */
.ask_super_host_flex {
  display: flex;
  flex-direction: column;
  padding-left: 50px;
}

/* Inspiration for future getaways SECTION */
.inspiration_for_future_gataways_section {
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 50px;
}

/* Inspiration for future getaways FLEX */
.inpiration_top_menu_flex {
  display: flex;
}

/* INSPIRATION COMPONENTS */
/* ASK SUPERHOST TITLE */
.ask_superhost_title {
  color: white;
  font-size: 70px;
  padding-top: 50px;
  line-height: 70px;
}

/* INPIRATION COMPONENTS  */
/* INPIRATION FOR FUTURE GATAWAYS COMPONENTS */
.inpiration_top_menu_flex > div {
  padding: 0 25px 0 0;
}

/* Inspiration for future getaways FOOTER 1 */
.destination_for_arts_and_culture_footer {
  display: flex;
  box-sizing: border-box;
  margin-top: 50px;
}

.inspiration_for_future_gateaways_title {
  margin-bottom: 25px;
}
.destination_for_arts_and_culture_footer > div {
  padding-right: 20%;
}

.footer_title,
.footer_name {
  /* font-weight: bold;*/
  width: 100px;
}

/* HIDE IN CSS */
.hide_in_css{
  display: none;
}

.ask_superhost_button {
  padding: 15px 10px;
  width: 180px;
  font-weight: bold;
  color: #222222;
  background-color: white;
  border-style: none;
