}

/* FOOTER2 SECTION */
.footer2_section {
  background-color: #f7f7f7;
  margin-top: 50px;
 
}

/* FOOTER2 FLEX */
.footer2_flex {
  display: flex;
  margin: 0px 0 0 50px;
  padding-top: 30px;
  box-sizing: border-box;
}

/* COPYRIGTH FOOTER SECTION */
.copyright_footer_section {
  background-color: #f7f7f7;
}

.copyright_footer_flex_container {
  padding-top: 50px;
  margin: 0 50px 0 50px;
  display: flex;
  
}

.copyright_footer_left_flex {
  display: flex;
  box-sizing: border-box;
  width: 30%;
  justify-content: center;
}

.copyright_footer_left_flex > div {
  padding: 0 4px;
}

.copyright_footer_left_flex > .wide_text {
  width: 130px;
}

.copyright_footer_right_flex {
  display: flex;
  margin-left: 40%;
  box-sizing: border-box;
}

.copyright_footer_right_flex > div,
i {
  padding: 10px;
}

.social_icon {
  margin-left: 30px;
}

.copyright_footer_flex_container {
  display: flex;
}

.copyright_www_icon {
  height: 15px;
  margin-top: 12px;
}

.date_input_box {
  border: transparent;
  border-left: 1px grey solid;
}

/* FOOTER 1 AND FOOTER COMPONENTS */
.footer1_hr {
  margin-top: 15px;
  color: rgb(189, 173, 173);
}

/* FOOTER2 COMPONENT */
.footer2_flex > div {
  padding-right: 7.8%;
}

.footer2_flex > div > div {
  width: 250px;
  padding-bottom: 12px;
}

.footer2_title {
  font-weight: bold;
}

.footer2_hr {
  margin-top: 15px;
  color: rgb(189, 173, 173);
  margin: 30px 20px 0px 20px;
}

.copyright_footer_section2{
  display: none;
}



